import DropdownCounter from "../../components/dropdown-counter/dropdown-counter.js";
import InputField from "../../components/input-field/input-field.js";
import DropdownDatepicker from "../../components/dropdown-datepicker/dropdown-datepicker.js";
import Expander from "../../components/expander/expander.js";
import LikeButton from "../../components/like-button/like-button.js";
import RangeSlider from "../../components/range-slider/range-slider.js";
import Pagination from "../../components/pagination/pagination.js";
import "./form-elements.scss";


(function($){

  const className = "form-elements";

  $(`.js-${className}__dropdown-counter`).each(function () {
    new DropdownCounter($(this));
  });

  $(`.js-${className}__input-field`).each(function () {
    new InputField($(this));
  });

  $(`.js-${className}__dropdown-datepicker`).each(function () {
    new DropdownDatepicker($(this));
  });

  $(`.js-${className}__expander`).each(function () {
    new Expander($(this));
  });

  $(`.js-${className}__like-button`).each(function () {
    new LikeButton($(this));
  });

  $(`.js-${className}__range-slider`).each(function () {
    new RangeSlider($(this));
  });

  $(`.js-${className}__pagination`).each(function () {
    new Pagination($(this), {
      itemsCount: 170,
    });
  });

})(jQuery);