class Square {
  #className = "square";

  #$component;

  constructor($parent) {
    this.#init($parent);
    this.#render();
  }

  show() {
    this.#$component.removeClass(`${this.#className}_hidden`);
  }

  hide() {
    this.#$component.addClass(`${this.#className}_hidden`);
  }

  #init($parent) {
    this.#$component = $parent.find(`.js-${this.#className}`);
  }

  #render() {
    this.#setHandlers();
  }

  #setHandlers() {
    this.#$component.on("click.square", this.#handleSquareClick.bind(this));
  }

  #handleSquareClick() {
    this.#$component.toggleClass(`${this.#className}_clicked`);
  }

}

export default Square;