import "paginationjs";

class Pagination {
  #className = "pagination";

  #$component;
  #$paginationContainer;

  #options;

  constructor($parent, options) {
    this.#init($parent, options);
  }

  #init($parent, options) {
    this.#$component = $parent.find(`.js-${this.#className}`);

    this.#$paginationContainer = this.#$component.find(`.js-${this.#className}__pagination-container`);

    this.#options = options;
    this.#initPaginationjs();
  }

  #initPaginationjs() {
    const { pageSize = 12 } = this.#options;

    this.#$paginationContainer.pagination({
      dataSource: this.#initData.bind(this),
      pageSize,
      pageRange: 1,
      autoHidePrevious: true,
      autoHideNext: true,
      callback: this.#renderData.bind(this),
    });
  }

  #initData(done) {
    const { itemsCount = 0 } = this.#options;
    const result = [];

    for (let i = 1; i <= itemsCount; i++) {
        result.push(i);
    }
    done(result);
  }

  #renderData(data) {

  }
}

export default Pagination;